import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { UserDataService } from '@services/modules/user/dataservices/user.data.service';

@Component({
    selector: 'tz-forgot-password',
    templateUrl: './forgot-password.component.html',
    styleUrls: ['./forgot-password.component.scss'],
})
export class ForgotPasswordComponent {
    public passwordChanged = false;

    constructor(private router: Router, private userDataService: UserDataService) {}

    public changePassword(email: string): void {
        this.passwordChanged = true;
        this.userDataService.forgotPassword(email).subscribe(
            () => {},
            (failure: any) => {
                console.log('Forgot password failure: ', failure);
            }
        );
    }

    public gotoHome(): void {
        this.router.navigate(['/search']);
    }
}
